.contact-us {
    padding-top: var(--section-padding);

    @media(min-width: $xxxl) {
        padding-bottom: 125px;
        background-image: url(../images/decor/contact-us-bg.svg);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
    }

    @media(max-width: $lg) {
        padding-top: var(--offset-xxxl);
    }

    // .contact-us__inner 

    &__inner {
        @media(min-width: $xxl) {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        @media(max-width: $lg) {
            @include row-grid(var(--offset-xxxl));
        }
    }

    // .contact-us__content

    &__content {
        @include row-grid(var(--offset-xxls));

        @media(min-width: $xxl) {
            padding-right: calc(145 / #{$container} * 100%);
        }

        @media(max-width: $sm) {
            row-gap: var(--offset-l);
        }
    }

    // .contact-us__title

    &__title {
        margin-bottom: var(--offset-s);
    }

    // .contact-us__form

    &__form {
        padding: var(--offset-xxl) var(--offset-xxxl);

        @media(min-width: $xxl) {
            flex: 0 0 528px;
        }

        @media(max-width: $ms) {
            padding: var(--offset-l) var(--offset-m);
        }

        .form {
            box-shadow: none;

            @media(max-width: $xxl) {
                padding: 0;
            }
        }
    }

    // .contact-us__form-title

    &__form-title {
        text-align: center;
        margin-bottom: var(--offset-l);
    }
}
