.mascot-section {
    @media(min-width: $xxxl) {
        margin-top: calc(var(--offset-xl) * (-1));
    }

    // .mascot-section__img

    &__img {
        @media(min-width: $xl) {
            flex: 0 0 592px;
        }

        @media(max-width: $lg) {
            margin-bottom: var(--offset-m);
        }
    }

    // .mascot-section__content

    &__content {
        @media(min-width: $xl) {
            position: relative;
            padding-top: 75px;
            padding-left: 82px;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100px;
                height: 79px;
                background-image: url(../images/decor/yellow-decor.svg);
            }
        }

        @media(min-width: $xxxl) {
            margin-left: 80px;
        }
    }
}
