.reviews-metrics {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: calc(var(--offset-m) * (-1));

    @media(min-width: $sm) {
        margin-left: calc(var(--offset-l) * (-1));
    }

    @media(min-width: $xl) {
        margin-left: calc(var(--offset-xl) * (-1));
    }

    > * {
        margin-bottom: var(--offset-m);
        min-width: 240px;

        @media(min-width: $sm) {
            margin-left: var(--offset-l);
            width: calc(50% - var(--offset-l));
        }

        @media(min-width: $xl) {
            margin-left: var(--offset-xl);
            width: calc(50% - var(--offset-xl));
        }

        @media(min-width: $xxxl) {
            width: calc(33.3333% - var(--offset-xl));
        }
    }
}

.metric-reviews {
    display: flex;
    align-items: center;
    // .metric-reviews__num

    &__num {
        position: relative;
        flex-shrink: 0;
        margin-right: var(--offset-m);
        width: 68px;
        height: 68px;
        border-radius: 50%;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            border-radius: 50%;
            box-shadow: var(--bsh);
        }

        span {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            @extend %museo-700;
            font-size: 20px;
        }
    }

    // .metric-reviews__content

    &__content {
        @include row-grid(var(--offset-xxxs));
        justify-items: start;
    }

    // .metric-reviews__title

    &__title {
        @extend %museo-700;
        font-size: 20px;
        white-space: nowrap;
    }

    // .metric-reviews__descr

    &__descr {
        @extend %museo-300;
    }
}
