.contacts {
	@include row-grid(var(--offset-m));

	&--style {
		&_flex {
			display: flex;
			flex-wrap: wrap;
			row-gap: var(--offset-xs);

			>* {
				margin-right: var(--offset-l);
			}
		}
	}
}

.contact {
	@extend %museo-300;
	display: inline-flex;
	align-items: flex-start;
	transition: var(--transition) color;

	.icon {
		margin-right: var(--offset-xs);
	}

	&__content {
		@include row-grid(var(--offset-xxs));
		justify-items: start;

		@media(max-width: $sm) {
			row-gap: var(--offset-xxxs);
		}
	}

	&__title {
		@extend %museo-500;
		font-size: 20px;

		@media(max-width: $sm) {
			font-size: 18px;
		}
	}
}

.mr-20 {
	margin-right: var(--offset-sm);
}

.contact-block {
	@include row-grid(var(--offset-m));

	&__title {
		font-size: 24px;

		@media(max-width: $sm) {
			font-size: 18px;
		}
	}

	&__phones {
		@include row-grid(var(--offset-xxs));
		color: var(--text-color);
		position: relative;
		padding-left: 44px;

		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: var(--icon-size);
			height: var(--icon-size);
			background-image: url(../images/icons/phone.svg);
		}

		b {
			@extend %museo-500;
			padding-right: 2px;
		}
	}
}

a.contact {
	@media (any-hover: hover) {
		&:hover {
			color: var(--pink);
		}
	}
}
