.certificates-section {

    // .certificates-section__inner

    &__inner {
        display: grid;
        gap: var(--offset-xxxl);

        @media(max-width: $ms) {
            gap: var(--offset-l);
        }

        @media(min-width: $xxxl) {
            grid-template-columns: 438px 1fr;
        }
    }

    // .certificates-section__content

    &__content {
        @include row-grid(var(--offset-m));
        justify-items: start;
        align-content: start;

        @media(max-width: $sm) {
            row-gap: var(--offset-s);
        }
    }

    // .certificates-section__definition

    &__definition {
        @extend %museo-500;
        font-size: 24px;

        @media(max-width: $sm) {
            font-size: 20px;
        }

        b {
            @extend %museo-700;
        }
    }
}

.certificate-preview {
    display: flex;
    flex-direction: column;
    padding-bottom: var(--offset-xxl);

    @media(max-width: $sm) {
        padding-top: var(--offset-s);
        padding-bottom: var(--offset-m);
    }

    &__top {
        margin-bottom: var(--offset-l);

        @media(max-width: $ms) {
            margin-bottom: 0;
            margin-top: var(--offset-m);
        }
    }

    @media(max-width: $ms) {
        flex-direction: column-reverse;
    }
}
