.aside-choose-tape {
    display: none;
    position: fixed;
    right: 0;
    bottom: var(--offset-s);
    left: 0;
    z-index: 25;
    padding: var(--offset-s) var(--container-padding);
    align-items: center;
    justify-content: space-between;
    background-color: var(--white);
    box-shadow: var(--bsh);

    @media(max-width: $xxl) {
        display: flex;
    }

	@media(max-width: $lg) {
		bottom: var(--bottom-bar-height);
	}

	&__value {
		color: var(--text-color);
		border-bottom: 1px solid var(--text-color);
	}

    &__btn {
        @include padding-y(var(--offset-s));
    }
}
