.price-block {
    @include row-grid(var(--offset-xs));
    align-content: start;
    justify-items: start;

    @media(max-width: $sm) {
        row-gap: var(--offset-xxxs);
    }

    // .price-block__title

    &__title {
        @extend %museo-500;
        font-size: 24px;
    }

    // .price-block__value

    &__value {
        @extend %museo-700;
        font-size: 20px;
        color: var(--text-color);
    }
}
