.aside-section {
	&__body {
		background-image: url(../images/decor/help-bg.jpg);
		background-size: cover;
		background-repeat: no-repeat;
		background-color: #FFF0F1;
		@include padding-y(104px);

		@media(max-width: $lg) {
			@include padding-y(var(--offset-xxxl));
		}

		@media(max-width: $sm) {
			background-image: url(../images/decor/help-bg-mob.jpg);
		}
	}

	// .need-help__inner

	&__inner {
		display: flex;
		align-items: center;

		@media(min-width: $xl) {
			justify-content: space-between;
		}

		@media(max-width: $lg) {
			flex-direction: column;
			align-items: flex-start;

			>* {
				&:not(:last-child) {
					margin-bottom: var(--offset-l);
				}
			}
		}

		@media(max-width: $sm) {
			align-items: center;
			text-align: center;
		}
	}

	&__content {
		@include row-grid(var(--offset-xxs));
		justify-items: start;
	}

	// .need-help__btn

	&__btn {
		justify-content: center;
		min-width: 285px;
	}
}
